<template>
  <div class="welcome">
    <el-card class="box-card">
        <div class="wel-header" slot="header">
            <i class="el-icon-message-solid"></i>
            <span>欢迎来到青年帮后台管理系统</span>
        </div>
        <div class="main">
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-card shadow="never">
                        <el-calendar>
                        </el-calendar>
                    </el-card>
                </el-col>
                <el-col :span="12">
                    <el-card class="box-card">
                        <div slot="header" class="tq-header">
                            <i class="el-icon-sunny"></i>
                            <span>近三天天气</span>
                        </div>
                        <div class="tq-main">
                            <el-card class="box-card" shadow="hover" v-for="(item,idx) in tqList" :key="idx">
                                <div class="innerItem">
                                    <div class="left">
                                        <img :src="gettqIcon(idx)" alt="">
                                        <p class="temperature">{{item.tempNight}}~{{item.tempDay}}</p>
                                        <div class="text">{{item.conditionDay}}/{{item.windDirDay}}</div>
                                    </div>
                                    <div class="right">
                                        <div class="day">{{getDay(idx)}}</div>
                                        <div class="date">{{item.predictDate}}</div>
                                    </div>
                                </div>
                            </el-card>
                        </div>
                    </el-card>
                </el-col>
            </el-row>
        </div>
    </el-card>
  </div>
</template>

<script>
import { tqData } from '@/network/networds'
export default {
  data () {
    return {
      tqList: []
    }
  },
  mounted () {
    // var sesstqData = window.sessionStorage.getItem('sesstqData');
    let sesstqData = window.sessionStorage.getItem('sesstqData')
    sesstqData = JSON.parse(sesstqData)

    // 有缓存
    if (sesstqData) {
      const oldDate = sesstqData.time
      const nowDate = new Date()
      if (oldDate - nowDate > 1000 * 60 * 60) {
        this.gettqData()
      } else {
        this.tqList = sesstqData.tqList
      }
      // 没有缓存
    } else {
      this.gettqData()
    }
  },
  methods: {
    // 获取天气图标
    gettqIcon (num) {
      return require('@/assets/images/tqlcon/W' + num + '.png')
    },
    // 获取具体天
    getDay (num) {
      let day
      if (num == 0) {
        day = '今天'
      } else if (num == 1) {
        day = '明天'
        return day
      } else if (num == 2) {
        day = '后天'
        return day
      } else {
        return ''
      }
    },
    // 获取天气数据
    gettqData () {
      tqData().then(res => {
        this.tqList = res.data.data.forecast
        const sesstqData = { tqList: this.tqList, time: new Date() }
        window.sessionStorage.setItem('sesstqData', JSON.stringify(sesstqData))
      })
    }
  }
}
</script>
<style lang="less" scope>
.wel-header{
    font-size: 25px;
    color:#409EFF;
    font-weight: 600;
    span{
        padding-left: 10px;
    }
}
.tq-header{
    font-size: 25px;
    color:#67C23A;
    i{
        font-size: 30px;
        padding-right: 10px;
    }
}
.tq-main{
    .innerItem{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .left{
            display: flex;
            justify-content: space-between;
            align-items: center;
            img{
                width: 40px;
                height: 40px;
                display: block;
                padding-right: 10px;
            }
            .temperature{
                padding-right: 15px;
                font-size: 35px;
                font-weight: 600px;
                position:relative;
            }
            .temperature:after{
                content:'';
                width:8px;
                height: 8px;
                position:absolute;
                top: 5px;
                right: 0px;
                border:3px solid #303133;
                box-sizing: border-box;
                border-radius: 50%;
            }
            .text{
                color:#409EFF;
                font-size: 12px;
                transform: translateY(10px);
            }
        }
        .right{
            .day{
                color:#67C23A;
                font-size: 30px;
                font-weight: 600;
            }
            .date{
                margin-top: 5px;
                font-size: 12px;
                color:#C0C4CC;
                letter-spacing: 2;
            }
        }
    }
}
</style>
